import request from '@/utils/request'
// 
export function finishedOrderNum(mapId) { return request({ url: '/analysis/order/finishedOrderNum', method: 'get', params: { mapId }}) }

// 
export function totalOrderNum(mapId) { return request({ url: '/analysis/order/totalOrderNum', method: 'get', params: { mapId }}) }

// 
export function getMapInfo(mapId) { return request({ url: '/map/getMapInfo', method: 'get', params: { mapId }}) }

// 
export function getMapLines(mapId) { return request({ url: '/map/getMapLines', method: 'get', params: { mapId }}) }

// 
export function getMapSpots(mapId) { return request({ url: '/map/getMapSpots', method: 'get', params: { mapId }}) }

// 
export function disconnectVehicles(mapId) { return request({ url: '/vehicle/disconnectVehicles', method: 'get', params: { mapId }}) }

// 
export function disableVehicle(vehicleId, disable) { return request({ url: '/vehicle/disableVehicle', method: 'get', params: { vehicleId, disable }}) }

// 
export function releaseVehicleOccupiedSpots(vehicleId) { return request({ url: '/trafficManagement/releaseVehicleOccupiedSpots', method: 'get', params: { vehicleId }}) }
