
import store from '@/store'

export default {
  inserted(el, binding) {
    let { value } = binding //
    if (value) {
      let permissions = store.getters.permissions  //
      let hasPermissions; //
      // 
      if (Array.isArray(value)) {
        hasPermissions = value.some(e => {
          return permissions.includes(e)
        });
        if (!hasPermissions) {
          el.parentNode && el.parentNode.removeChild(el)
        }
      } else if (value[0] === "!") {
        // "!"
        let hasPermissions = permissions.includes(value.slice(1,))
        // DOM
        if (hasPermissions) {
          el.parentNode && el.parentNode.removeChild(el)
        }
      } else {
        // 
        let hasPermissions = permissions.includes(value)
        // DOM
        if (!hasPermissions) {
          el.parentNode && el.parentNode.removeChild(el)
        }
      }
    } else {
      throw new Error('')
    }
  }
}
